    border-radius: 5px;
    margin-bottom: 2em;
    height: 1.2em;
    width: 80%;
    color: #D5D5D5;
    padding: 10px;
    font-size: 1rem;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
}

.form-submit {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border: 1px solid #FFFFFF;
    height: 2.2em;
    font-size: 1rem;
    background-color: #0E0E0E;
    color: white;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    width: 10em;
}

.form-close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: rgba(255, 255, 255, 0.1);
    border: none;
    height: 30px;
    width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.close-img {
    height: 22px;
    width: 22px;
}

.top-right {
    position: absolute;
    top: 0px;
    right: 0px;
    color: white;
}

.bottom-left {
    position: absolute;
    bottom: 0px;
    left: 0px;
    color: white;
}

@media (max-width:900px) {
    .body::-webkit-scrollbar {
        display: none;
    }

    .body {
        -ms-overflow-style: none;
        scrollbar-width: none;
    }

    .container {
        flex-direction: column;
    }

    .image {
        width: 80%;
        margin-top: 100px;
    }

    .form {
        width: 80%;
        margin-bottom: 50px;
    }

    .form-feilds {
        width: 70%;
    }

    .form-submit {
        margin-bottom: 2em;
    }
}

@media (min-width:901px) {
    .body {
        overflow: hidden;
        height: 100vh;
        width: 100%;
    }

    .container {
        height: 100vh;
        width: 100%;
    }
}
