.card-container{
    color:white;
    background-image:url("../images/image87.png");
    width:450px;
    height:200px;
    margin-top: 20px;
    margin-bottom: 10px;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid white;
}
.event-details{
    height:80%;
    width: 100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.event-link{
    height:20%;
    width:100%;
    border-color: white;
    border-top: 1px solid;
    display:flex;
    justify-content: center;
    align-items: center;
}
.event-details{
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.title {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 20px;
}
.detail{
    display:flex;
    flex-direction: column;
    align-items: flex-start;
}
.title-text{
    font-size: 1rem;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
}
.detail-text{
    font-size: 1rem;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
}
@media (max-width:560px){
    .card-container{
        width:100%;
    }
}