@import url('https://fonts.googleapis.com/css?family=Inter');
@import url('https://fonts.googleapis.com/css?family=Henny+Penny');

.body {
    background-image: url("../images/image83.png");
    font-family: 'Inter';
    min-height: 100vh;
}

.body::-webkit-scrollbar {
    display: none;
}

.body {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    width: 100%;
}

.head-div {
    width: 90%;
    background: #000000;
    box-shadow: 0px 4px 20px rgba(255, 255, 255, 0.25);
    padding: 0px;
    margin-top: 100px;
}

.heading {
    font-family: 'Henny Penny';
    font-style: normal;
    font-weight: 400;
    font-size: 2rem;
    text-align: center;
    letter-spacing: 0.13em;
    color: #FFFFFF;
}

.content {
    width: 90%;
    min-height: 80vh;
    background: #1A1919;
    box-shadow: 0px 10px 40px 40px rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 100px;
}

.user-side {
    width: 90%;
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.user-img {
    background-image: url("../images/Ellipse1.png");
    height: 130px;
    width: 130px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

.user-profile {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    width: 45%;
}

.user-details {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin-left: 20px;
}

.user-name {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    color: white;
    margin-bottom: 10px;
}

.user-contact {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.details-holder {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.user-icons-username {
    height: 25px;
    width: 25px;
    background-color: transparent;
    background-image: url("../images/username.png");
    background-size: cover;
}

.user-icons-email {
    height: 25px;
    width: 25px;
    background-color: transparent;
    background-image: url("../images/mail.png");
    background-size: cover;
}

.user-contact-details {
    font-size: 0.75rem;
    color: white;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    margin-left: 3px;
}

.blitz-logo {
    background-image: url("../images/Blitz_Logo_23.png");
    background-size: contain;
    background-repeat: no-repeat;
    width: 45%;
    height: 130px;
    background-position: right;
}

.separator {
    width: 90%;
    border: 1px solid #FFFFFF;
    height: 0.01px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.event-side {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-bottom: 50px;
}

.event-head {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 800;
    color: #FFFFFF;
    font-size: 1.75rem;
    margin-bottom: 30px;
}

.event-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
    align-self: center;
}

@media (max-width:1000px) {
    .blitz-logo {
        display: none;
    }

    .user-profile {
        width: 100%;
    }

    .event-list {
        display: flex;
        flex-direction: column;
        width: 100%;
        align-items: center;
        justify-content: center;
    }
}